<template>
  <div>
    <div class="black">
      <div
        v-if="$route.query.accountAmount > 0"
        class="f22"
      >{{ $route.query.accountAmount | point(2) }}</div>
      <div
        v-else
        class="f22"
      >0</div>
      <div class="m18">账户余额(元)</div>
      <div class="flex">
        <div
          class="button"
          @click="openShow('转入')"
        >转入</div>
        <div
          class="button"
          @click="openShow('转出')"
        >转出</div>
        <div
          class="button"
          @click="openShow('调整')"
        >调整</div>
      </div>
    </div>
    <div class="f16">账户明细</div>
    <div
      class="f16 time_flex"
      @click="show_time = true"
    >{{ d_createTime }}
      <van-icon
        style="vertical-align:middle;"
        name="arrow-down"
      />
    </div>
    <div
      class="data_box"
      style="background:#fafafa;"
    >
      <div class="w1">日期</div>
      <div class="w2">项目</div>
      <div class="w3">收支金额</div>
      <div class="w4">账户结余</div>
      <div>操作</div>
    </div>
    <van-pull-refresh
      class="overflow"
      v-model="pullLoading"
      @refresh="onPullRefresh"
      success-text="刷新成功"
    >
      <van-list
        v-model="listLoading"
        @load="onLoadList"
        :offset="30"
        :finished="finishedList"
        finished-text="没有更多了"
      >
        <!-- <div class="overflow"> -->
        <div
          v-for="(item,i) in listData"
          :key="i"
          class="data_box"
        >
          <div class="w1">{{ item.createTime | dataFilter('MM-DD') }}</div>
          <div
            v-if="hotelPayeeRecordType_obj"
            class="w2"
          >{{ hotelPayeeRecordType_obj[item.billType] }}</div>
          <div
            :class="item.amount > 0 ? 'bc6c' : 'ff5'"
            class="w3"
          >{{ item.amount > 0 ? '+' : '-' }}¥{{ Math.abs(item.amount) | point }}</div>
          <div class="w4">{{ item.leftAmount | point }}</div>
          <div
            class="ff4"
            @click="goNext(item)"
          >查看</div>
        </div>
        <!-- </div> -->
      </van-list>
    </van-pull-refresh>
    <van-popup
      v-model="show_time"
      :close-on-click-overlay='false'
      position="bottom"
      style='width:100%;'
    >
      <van-datetime-picker
        v-model="currentDate"
        type="year-month"
        title="选择年月"
        :min-date="minDate"
        :max-date="maxDate"
        :formatter="formatter"
        @confirm="onConfirmTime"
        @cancel="show_time = false"
      />
    </van-popup>
    <van-popup
      v-model="charge_refund_show"
      class="show-box"
    >
      <div class="show-box-content">
        <p class="fw">{{ typeTitle }}金额</p>
        <div
          v-if="typeTitle == '调整'"
          class="pay-room none"
        >
          <div>调整方式</div>
          <van-radio-group
            v-model="myRadio"
            direction="horizontal"
            checked-color='#ff7d41'
          >
            <van-radio name="1">调增</van-radio>
            <van-radio name="2">调减</van-radio>
          </van-radio-group>
        </div>
        <div
          v-if="typeTitle != '调整'"
          class="pay-room none"
        >
          <div>{{ typeTitle }}账户</div>
          <div
            class="border display"
            @click="payeeAccount_show = true"
          >
            <input
              class="center"
              readonly
              placeholder="请选择账户"
              type="text"
              v-model="patargetAccountLabel"
            >
            <van-icon name="arrow-down" />
          </div>
        </div>
        <div class="pay-room none">
          <div>{{ typeTitle }}金额</div>
          <div class="border display">
            <input
              class="center"
              placeholder="请输入金额"
              type="text"
              v-model="btnFormData.accountAmount"
            >
          </div>
        </div>
        <div
          v-if="typeTitle == '调整'"
          class="pay-room none"
        >
          <span>{{ typeTitle }}原因</span>
          <div class="border display">
            <input
              class="center"
              placeholder="请输入原因"
              type="text"
              v-model="btnFormData.remark"
            >
          </div>
        </div>
        <van-button
          class="show-button"
          @click="myCommit"
        >确定{{ typeTitle }}</van-button>
      </div>
    </van-popup>
    <van-popup
      style="width: 100%"
      v-model="payeeAccount_show"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="users"
        @confirm="onConfirmUser"
        @cancel="payeeAccount_show = false"
      />
    </van-popup>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotelPayeeAccountRecord, GetBusinessHotelPayeeAccountByhotel, PostBusinessHotelPayeeAccountIn, PostBusinessHotelPayeeAccountOut, PostBusinessHotelPayeeAccountTrim } from '@/api/api_business';
import { Toast } from 'vant';
export default {
    data() {
        return {
            listData: [],
            listLoading: false, //  上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, //  上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            dataForm: {
                hotelPayeeAccountId: this.$route.query.id,
                pageNum: 0,
                pageSize: 10,
                createTime: null
            },
            btnFormData: {
                hotelPayeeAccountId: this.$route.query.id,
                targetAccountId: null,
                accountAmount: null,
                remark: null
            },
            users: [],
            businessHotelPayeeRecordType: [],
            hotelPayeeRecordType_obj: {},
            myRadio: '1',
            minDate: null,
            maxDate: null,
            currentDate: null,
            d_createTime: null,
            typeTitle: null,
            patargetAccountLabel: null,
            show_time: false,
            payeeAccount_show: false,
            charge_refund_show: false
        };
    },
    watch: {
        'btnFormData.accountAmount': {
            handler() {
                if (this.typeTitle == '转出' && this.btnFormData.accountAmount > this.$route.query.accountAmount) {
                    Toast({
                        message: `转出金额不大于${this.$route.query.accountAmount}`,
                        duration: 900
                    });
                    this.btnFormData.accountAmount = null;
                    return;
                }
                if (this.typeTitle == '转入' && this.btnFormData.accountAmount > this.c_accountAmount) {
                    Toast({
                        message: `转入金额不大于${this.c_accountAmount}`,
                        duration: 900
                    });
                    this.btnFormData.accountAmount = null;
                    return;
                }
            }
        }
    },
    created() {
        this.maxDate = new Date(dayjs());
        this.minDate = new Date(dayjs().subtract(3, 'year'));
        this.d_createTime = dayjs().format('YYYY年MM月');
        this.currentDate = new Date(dayjs());
        this.dataForm.createTime = dayjs().format('YYYY-MM');
        this.$store.commit('setHeader', this.$route.query.accountName);
        this.getType();
    },
    methods: {
        getType() {
            GetBusinessHotelPayeeAccountByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.userOldAll = res.data;
                this.users = res.data.filter((item) => item.id != this.$route.query.id).map((item) => item.accountName);
                GetSystemDictDataByType({ dictType: 'business_hotel_payee_record_type' }).done((res) => {
                    this.hotelPayeeRecordType_obj = {};
                    this.businessHotelPayeeRecordType = res.data.map((item) => {
                        this.hotelPayeeRecordType_obj[item.dictValue] = item.dictLabel;
                    });
                    // this.onLoadList();
                });
            });
        },
        formatter(type, val) {
            if (type ==
 'year') {
                return `${val}年`;
            } else if (type === 'month') {
                return `${val}月`;
            }
            return val;
        },
        onConfirmTime(e) {
            this.show_time = false;
            this.currentDate = this.d_createTime = dayjs(e).format('YYYY年MM月');
            this.dataForm.createTime = dayjs(e).format('YYYY-MM');
            this.listData = [];
            this.dataForm.pageNum = 0;
            this.onLoadList();
        },
        openShow(type) {
            this.btnFormData.accountAmount = null;
            this.btnFormData.targetAccountId = null;
            this.patargetAccountLabel = null;
            this.typeTitle = type;
            if (this.$route.query.accountAmount == '0' && this.typeTitle == '转出') {
                Toast({
                    message: '当前账户余额不足',
                    duration: 1000
                });
                return;
            }
            this.charge_refund_show = true;
        },
        onConfirmUser(val) {
            this.btnFormData.accountAmount = null;
            this.patargetAccountLabel = val;
            this.btnFormData.targetAccountId = this.userOldAll.find((item) => item.accountName == val).id;
            this.c_accountAmount = this.userOldAll.find((item) => item.accountName == val).accountAmount;
            this.payeeAccount_show = false;
            if (this.c_accountAmount == '0' && this.typeTitle == '转入') {
                Toast({
                    message: '转入账户余额不足，请重新选择',
                    duration: 1000
                });
                return;
            }
        },
        //  下拉刷新列表
        onPullRefresh() {
            this.listData = [];
            this.dataForm.pageNum = 0;
            this.finishedList = false;
            // this.onLoadList();
        },
        //  上滑加载列表
        onLoadList() {
            GetBusinessHotelPayeeAccountRecord(this.dataForm).done((res) => {
                this.listData = this.listData.concat(res.data);
                this.listLoading = this.pullLoading = false;
                if (res.total == this.listData.length) {
                    this.finishedList = true;
                } else if (res.total > this.listData.length) {
                    this.finishedList = false;
                }
            });
            this.dataForm.pageNum++;
        },
        myCommit() {
            let _api = null;
            if ((this.typeTitle == '转入' && !this.btnFormData.targetAccountId) || (this.typeTitle == '转出' && !this.btnFormData.targetAccountId)) {
                Toast(`请选择${this.typeTitle}账户`);
                return;
            }
            if (this.typeTitle == '调整') {
                _api = PostBusinessHotelPayeeAccountTrim;
                this.btnFormData.accountAmount = this.myRadio == '1' ? this.btnFormData.accountAmount : '-' + this.btnFormData.accountAmount;
                delete this.btnFormData.targetAccountId;
            } else if (this.typeTitle == '转入') {
                _api = PostBusinessHotelPayeeAccountIn;
            } else if (this.typeTitle == '转出') {
                _api = PostBusinessHotelPayeeAccountOut;
            }
            if (!this.btnFormData.accountAmount) {
                Toast(`请输入${this.typeTitle}金额`);
                return;
            }
            _api(this.btnFormData).done((res) => {
                this.$route.query.accountAmount = res.data.accountAmount;
                this.charge_refund_show = false;
                Toast(res.msg);
                this.listData = [];
                this.dataForm.pageNum = 0;
                this.onLoadList();
            });
        },
        goNext(item) {
            this.$router.push({
                path: '/payeeDetailPage',
                query: {
                    billType: this.hotelPayeeRecordType_obj[item.billType],
                    createTime: item.createTime,
                    person: item.creator,
                    amount: Number(item.amount),
                    leftAmount: Number(item.leftAmount)
                }
            });
        }
    }
};
</script>
<style scoped>
.data_box {
    display: flex;
    font-size: 14px;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
}
.overflow {
    overflow: scroll;
    height: calc(100% - 350px);
    position: fixed;
    right: 0;
    left: 0;
    bottom: 0;
    top: 330px;
}
table {
    margin: 0;
    font-size: 14px;
    line-height: 30px;
}
table tbody {
    overflow: scroll;
    height: 20%;
    position: fixed;
    background: #fff;
    top: 330px;
}
table > tr:first-of-type {
    background: #fafafa;
}
th {
    color: #9e9e9e;
    text-align: left;
    padding-left: 10px;
    font-weight: normal;
}
td {
    padding-left: 10px;
    text-align: left;
}
.black {
    background: #000;
    padding: 30px 0;
    color: #fff;
    font-size: 14px;
}
.flex {
    display: flex;
    justify-content: space-evenly;
}
.button {
    background: #fff;
    color: #000;
    border-radius: 18px;
    width: 24%;
    line-height: 28px;
    font-size: 12px;
    font-weight: bold;
}
.f22 {
    font-size: 22px;
}
.f16 {
    font-weight: bold;
    font-size: 15px;
    text-align: left;
    padding: 8px 10px;
}
.time_flex {
    background: #fff;
    font-weight: normal;
}
.m18 {
    margin: 18px 0;
}
.show-box {
    width: 90%;
    border-radius: 10px;
    font-size: 14px;
    color: rgba(0, 0, 1);
}
.show-box-content {
    padding: 1rem;
}
.show-button {
    width: 100%;
    background: #ff7d41;
    color: #fff;
    height: 2.5rem;
    margin-top: 1rem;
}
.pay-room {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
    border-bottom: 1px solid #e5e5e5;
}
.none {
    border: none;
    line-height: 40px;
    padding: 0.5rem 0;
    width: 100%;
}
.fw {
    font-weight: 600;
    font-size: 16px;
    padding: 0.5rem 0 1rem;
}
.border {
    border: 1px solid #d7d7db;
    border-radius: 5px;
    width: 60%;
}
.display {
    display: flex;
    align-items: center;
    justify-content: center;
}
.center {
    text-align: left;
    width: 60%;
}
.w1 {
    width: 18%;
}
.w2 {
    width: 20%;
}
.w3 {
    width: 26%;
}
.w4 {
    width: 25%;
}
.bc6c {
    color: #02bc6c;
}
.ff5 {
    color: #ff5257;
}
.ff4 {
    color: #ff7d41;
}
</style>



